h1 {
  margin-top: 0;
}

.puzzle3page {
  background-color: #543e89;
  background-size: cover;
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.time {
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  margin-left: 41.666666666666664vw;
  margin-right: 41.666666666666664vw;
  border-radius: 1.0416666666666667vw;
}

.puzzle3win {
  display: grid;
  grid-column: 1 / span 10;
  grid-row: 1 / span 10;
  width: 100%;
  object-fit: contain;
  max-height: 100%;
  max-width: 100%;
}

.puzzle3go {
  display: grid;
  grid-column: 1 / span 10;
  grid-row: 1 / span 10;
  width: 100%;
  object-fit: contain;
  max-height: 100%;
  max-width: 100%;
}

.puzImg3 {
  display: grid;
  grid-column: 1 / span 10;
  grid-row: 1 / span 10;
  object-fit: cover;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
}

.restart {
  height: 5.555555555555555vw;
  width: 13.88888888888889vw;
  color: white;
  font-size: 3.4722222222222223vw;
  background-color: green;
  border-radius: 1.0416666666666667vw;
}

.next {
  height: 5.555555555555555vw;
