  color: white;
  font-size: 3.4722222222222223vw;
  background-color: green;
  border-radius: 1.0416666666666667vw;
}

.puz3 {
  display: grid;
  grid-template-columns: repeat(10, 6.3%);
  grid-template-rows: repeat(10, 10%);
  justify-content: center;
  object-fit: cover;
  width: 100%;
  max-width: 100%;
  max-height: 100%;
}

